@import url(https://fonts.googleapis.com/css?family=Baloo+Da+2|Galada&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{background:url(https://images.unsplash.com/photo-1568899466260-b6d4e061856f?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=751&q=80);background-attachment:fixed;background-size:cover;background-repeat:no-repeat;height:100vh}.App{height:100vh}.Tab{grid-area:tab;display:grid;grid-template-columns:1fr 8fr 1fr;grid-template-rows:1fr 1fr auto 1fr;grid-template-areas:". . ." ". nav ." ". text ." ". . ."}.Buttons{grid-area:nav;display:flex;flex-direction:row;align-content:center;background-color:#88c868}.Buttons button{font-family:"Galada",cursive;font-size:1.5em;font-weight:lighter;background-color:#7abb5a;transition:border 300ms ease-in-out;transition:color 300ms ease-in-out;transition:transform 200ms ease-in-out;cursor:pointer;font-weight:bold;color:#ffffff;border:2px solid #7abb5a}.Buttons button:hover{transform:scale(1.1, 1.1);border:2px solid #6aaa4a;color:#366e1a}.Buttons button:focus{outline:0}.Home,.Menu,.About{min-height:70vh;background-color:#ffffffe7;border:2px solid #88c868;grid-area:text;padding:0 25px;padding-bottom:20px}.Home h1,.Menu h1,.About h1{margin:10px 0;font-family:"Galada",cursive;font-size:2em;color:#88c868}.Home h2,.Menu h2,.About h2{font-family:"Baloo Da 2",cursive;margin:5px 0;font-size:1.2em;color:#757575}.Home h2 span,.Menu h2 span,.About h2 span{color:#88c868;font-weight:bold}.Home p,.Menu p,.About p{font-family:"Baloo Da 2",cursive;font-size:1.3em;margin:5px 0;color:#88c868}.Menu .Display{display:grid;grid-gap:2rem;gap:2rem;grid-template-columns:repeat(auto-fill, minmax(160px, 1fr));grid-template-rows:auto}.Menu .Display .Card{display:flex;flex-direction:column;justify-content:space-between;height:245px;text-align:center;border:2px solid #88c868}.Menu .Display .Card h2{color:#000000}.Menu .Display .Card img{height:120px}@media (max-width: 524px){.Menu .Display .Card{height:300px}.Menu .Display .Card img{height:200px}}

